body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.banner {
  background-color: #6278F7;

  text-align: center;
}

.banner img {
  max-width: 100%;
}
footer {
  background: url(/organo/static/media/fundo-footer.402a39b8.png) no-repeat bottom / contain fixed #6278F7;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 80px 165px;
}

.social-media {
  display: flex;
  grid-gap: 38px;
  gap: 38px;
}

.social-media a {
  cursor: pointer;

  height: 32px;
  width: 32px;
}

footer > img {
  width: 166px;
}

footer span {
  color: #FFF;

  font-family: 'Montserrat', sans-serif;
  font-size: 1.25rem;
  font-weight: 500;

  line-height: 1.5rem;
}

@media screen and (min-width: 360px) and (max-width: 1120px) {
  footer {
    flex-direction: column;
    grid-gap: 2.5rem;
    gap: 2.5rem;
    padding: 40px 20px;
  }

  .social-media {
    grid-gap: 45px;
    gap: 45px;
  }

  .social-media a, .social-media a > img {
    height: 25px;
    width: 25px;
  }

  footer > img {
    width: 120px;
  }

  footer span {
    font-size: .75rem;
  }
}
.botao {
    background-color: #6278F7;
    border: none;
    border-radius: 10px;
    
    color: #fff;

    cursor: pointer;

    font-size: 1.125rem;
    font-weight: 700;

    margin: 2rem 0;

    padding: 2rem;
}

.botao:hover {
    color: #95FFD4;
}

@media screen and (min-width: 360px) and (max-width: 640px) {
    .botao {
        padding: 1.575rem;

        width: 100%;
    }
}
.campo_texto {
  margin: 24px 0;
}

.campo_texto label {
  display: block;

  font-size: 1.5rem;

  margin-bottom: .5rem;
}

.campo_texto input {
  background-color: #fff;
  border: none;
  border-radius: 10px;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  
  font-size: 1.5rem;

  padding: 1.5rem;

  width: 100%;
}

@media screen and (min-width: 360px) and (max-width: 640px) {
  .campo_texto label {
    font-size: 1.25rem;
  }
  
  .campo_texto input {
    font-size: 1.25rem;

    padding: 1.5rem 1rem;
  }
}
/* .lista_suspensa {
    
} */

.lista_suspensa label {
    display: block;

    font-size: 1.5rem;

    margin-bottom: .5rem;
}

.lista_suspensa select {
    background-color: #fff;
    border: none;
    border-radius: 10px;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
    box-sizing: border-box;
    
    font-size: 1.5rem;

    padding: 1.5rem;

    width: 100%;
}

@media screen and (min-width: 360px) and (max-width: 640px) {
    .lista_suspensa label {
        font-size: 1.25rem;
      }
      
      .lista_suspensa select {    
        font-size: 1.25rem;
    
        padding: 1.5rem 1rem;
      }
}
.formulario {
  display: flex;
  justify-content: center;

  margin: 80px 0;
}

.formulario form {
  background-color: #F2F2F2;
  border-radius: 20px;
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.08);

  padding: 2.25rem 4rem;

  max-width: 80%;
}

@media screen and (min-width: 360px) and (max-width: 640px) {
  .formulario form {
    border-radius: 0;

    padding: 2rem 1.75rem;

    max-width: 100%;
  }

  .formulario form > h2 {
    text-align: center;
  }
}
.colaborador {
  width: 280px;
}

.colaborador_cabecalho {
  /* background-color: #F0F0F0; */
  border-radius: 10px 10px 0px 0px;
}

.colaborador_cabecalho img {
  border-radius: 50%;

  position: relative;
  bottom: -50px;

  width: 100px;
}

.colaborador_rodape {
  background-color: #FFF;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);

  padding-bottom: 2.5rem;
  padding-top: 5.625rem
}

.colaborador_rodape h4 {
  font-size: 1.125rem;
  font-weight: bold;

  line-height: 1.375rem;
  
  margin-bottom: .5rem;
}

.colaborador_rodape h5 {
  color: #212121;

  font-size: 18px;

  line-height: 1.375rem;

  padding: 0 1rem;
}
.time {
  /* background-color: #D9F7E9; */

  padding: 2rem;

  text-align: center;
}

.time h3 {
  border-bottom: 4px solid;

  display: inline-block;

  font-size: 2rem;

  padding-bottom: .5rem;
}

.time .colaboradores {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  margin-top: 2rem;
}
